.input {
  max-width: 240px;
  width: 100%;
  height: 56px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  color: #000;
  padding-left: 15px;
  font-family: Karla;
  font-size: 18px;
  font-weight: 700;
}
.input::placeholder {
  color: rgba(0, 0, 0, 0.5);
  font-family: Roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
