.footer {
  padding: 20px 0;
  text-align: center;
  background: #fff;
  box-shadow: -2px 0px 8px 2px rgba(0, 0, 0, 0.1);
}
.footerText {
  position: relative;
  color: #212121;
  font-family: Karla;
  font-size: 18px;
  font-weight: 700;
}
.circle {
  position: relative;
  padding-right: 10px;
}
.circle::after {
  content: "";
  width: 5px;
  height: 5px;

  bottom: 0;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background: rgb(0, 255, 98);
  position: absolute;
}
