.header {
  padding: 5px 0;

  background: #fff;
  box-shadow: -2px 0px 8px 2px rgba(0, 0, 0, 0.1);
}

.logo {
  width: 46px;
  height: 49px;
}
.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.favorits, .currentUser {
  color: #000;
  text-align: center;
  font-family: Karla, sans-serif;
  font-size: 18px;
  font-weight: 700;
}
.heart {
  max-width: 20px;
}
.btn {
  color: #000;
  text-align: center;
  font-family: Karla, sans-serif;
  font-size: 18px;
  font-weight: 700;
}
 

