.card {
    position: relative;
}
.article {
  max-width: 240px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-self: stretch;
  border-radius: 8px 8px 4px 4px;
  background: #FFF;
  box-shadow: -2px 0px 8px 2px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  min-height: 280px;
}
.img {
  max-height: 167px;
  max-width: 240px;
  display: block;
  object-fit: cover;
  
}
.articleBottom {
  padding: 12px 16px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.name {
  color: rgba(0, 0, 0, 0.87);
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0.15px;
}
.species {
  color: rgba(0, 0, 0, 0.6);
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0.25px;
}
.button{
  position: absolute;
  right: 15px;
  top: 15px;
  scale: 1.3;
}