.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.btnBack {
  position: absolute;
  left: 0;
  top: 42px;
}
.btnHeart {
  position: absolute;
  right: 0;
  top: 42px;
  scale: 1.2;
}
.info {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  margin-top: 36px;
}
.title {
  color: #081f32;
  text-align: center;
  font-feature-settings: "liga" off, "clig" off;
  font-family: Roboto;
  font-size: 48px;
  font-weight: 400;
  margin-top: 16px;
}
.img {
  max-width: 300px;
  max-height: 300px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  display: block;
  margin-top: 16px;
}
.blocktitle {
  color: #8e8e93;
  font-feature-settings: "liga" off, "clig" off;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.15px;
  justify-content: center;
  margin-bottom: 42px;
}
.itemWrapper {
  margin-bottom: 20px;
}
