.image {
  width: 600px;
  height: 200px;
  margin-top: 26px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gridWrapper {
  margin-top: 60px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 20px;
  row-gap: 24px;
  margin-bottom: 48px;
}

.button {
  display: flex;
  width: 154px;
  height: 36px;
  padding: 10px 16px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-shrink: 0;
  color: #2196f3;
  text-align: center;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  border-radius: 4px;
  background: #f2f9fe;
  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2),
    0px 1px 18px 0px rgba(0, 0, 0, 0.12), 0px 6px 10px 0px rgba(0, 0, 0, 0.14);
  margin-bottom: 44px;
}

.filters {
  display: flex;
  width: 100%;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
}
.error {
  color: red;
  margin-top: 40px;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 1.25px;
  text-transform: uppercase;
}
.selectStyle {
  display: flex;
  justify-content: space-between;
  height: 56px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  padding-left: 16px;
  color: #000;
  text-align: center;
  font-family: Karla;
  font-size: 18px;
  font-weight: 700;
}

